<template>
    <el-form ref="form1" label-width="90px" >
        <el-row type="flex" class="row-bg" justify="space-between">
            <el-col :span="5">
                <el-form-item label="客户姓名">
                    <span v-if="orgUserBasicInfoView.name">
                    	{{orgUserBasicInfoView.name}}
                    </span>
                    <span v-else class="yellow-color">未完善</span>
                    <span v-if="!!isOtherVersion[1]" @click="showOtherVersion(1,'name')" class="link-text">查看</span>
                </el-form-item>
            </el-col>
            <el-col :span="5">
                <el-form-item label="性别" label-width="50px">
                    <span v-if="orgUserBasicInfoView.sex">
                    	{{orgUserBasicInfoView.sex}}
                    </span>
                    <span v-else class="yellow-color">未完善</span>
                    <span v-if="!!isOtherVersion[2]" @click="showOtherVersion(2,'sex')" class="link-text">查看</span>
                </el-form-item>
            </el-col>
            <el-col :span="5">
                <el-form-item label="年龄" label-width="50px">
                    <span v-if="orgUserBasicInfoView.age">
                    	{{orgUserBasicInfoView.age}}
                    </span>
                    <span v-else class="yellow-color">未完善</span>
                    <span v-if="!!isOtherVersion[3]" @click="showOtherVersion(3,'age')" class="link-text">查看</span>
                </el-form-item>
            </el-col>
            <el-col :span="5">
                <el-form-item label="居住城市" label-width="70px">
                    <span v-if="orgUserBasicInfoView.city_name">
                    	{{orgUserBasicInfoView.city_name}}
                    </span>
                    <span v-else class="yellow-color">
                    	未完善
                    </span>
                    <span v-if="!!isOtherVersion[4]" @click="showOtherVersion(4,'city')" class="link-text">查看</span>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row justify="space-around">
            <el-col :span="5">
                <el-form-item label="微信号">
                    <span v-if="orgUserBasicInfoView.wechat_code">
                    	{{orgUserBasicInfoView.wechat_code}}
                    </span>
                    <span v-else class="yellow-color">未完善</span>
                    <span v-if="!!isOtherVersion[5]" @click="showOtherVersion(5,'wechat_code')" class="link-text">查看</span>
                </el-form-item>
            </el-col>
            <el-col :span="18">
                <el-row justify="start">
                    <el-col :span="7" justify="space-around">
                        <el-form-item>
                            <el-checkbox
                                :true-label="1"
                                :value.number="orgUserBasicInfoView.add_phone"
                                disabled>
                                是否已添加到工作手机
                            </el-checkbox>
                        </el-form-item>
                    </el-col>
                    <el-col :span="7">
                        <el-form-item>
                            <el-checkbox
                                :true-label="1"
                                :value.number="orgUserBasicInfoView.add_flag"
                            disabled>
                                工作手机微信号是否已添加标注
                            </el-checkbox>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-col>
        </el-row>

        <el-row justify="space-around">
            <el-col :span="6">
                <el-form-item label="手机号码1">
                    <span
                    	class="cursor-pointer"
                    	v-if="orgUserBasicInfoView.mobile"
                    	@click="getWholeMobile('mobile',1,1);">
                    	{{orgUserBasicInfoView.mobile}}
                    </span>
                    <span v-else class="yellow-color">
                    	未完善
                    </span>
                    <span v-if="!!isOtherVersion[6]" @click="showOtherVersion(6,'mobile')" class="link-text">查看</span>
                </el-form-item>
            </el-col>
            <el-col :span="6">
                <el-form-item label="手机号码2">
                    <span
                    	class="cursor-pointer"
                    	v-if="orgUserBasicInfoView.mobile1"
                    	@click="getWholeMobile('mobile1',2,1);">
                    	{{orgUserBasicInfoView.mobile1}}
                    </span>
                    <span v-else class="yellow-color">
                    	未完善
                    </span>
                    <span v-if="!!isOtherVersion[7]" @click="showOtherVersion(7,'mobile1')" class="link-text">查看</span>
                </el-form-item>
            </el-col>
            <el-col :span="6">
                <el-form-item label="固定号码">
                    <span
                    	class="cursor-pointer"
                    	v-if="orgUserBasicInfoView.phone"
                    	@click="getWholeMobile('phone',3,1);">
                    	{{orgUserBasicInfoView.phone}}
                    </span>
                    <span v-else class="yellow-color">
                    	未完善
                    </span>
                    <span v-if="!!isOtherVersion[8]" @click="showOtherVersion(8,'phone')" class="link-text">查看</span>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row justify="space-around">
            <el-col :span="24">
                <el-form-item label="职业">
                    <span v-if="orgUserBasicInfoView.profession">
                    	{{orgUserBasicInfoView.profession}}
                    </span>
                    <span v-else class="yellow-color">
                    	未完善
                    </span>
                    <span v-if="!!isOtherVersion[9]" @click="showOtherVersion(9,'profession_id')" class="link-text">查看</span>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row justify="space-around">
            <el-col :span="24">
                <el-form-item label="职位">
                    <span v-if="orgUserBasicInfoView.job">
                    	{{orgUserBasicInfoView.job}}
                    </span>
                    <span v-else class="yellow-color">
                    	未完善
                    </span>
                    <span v-if="!!isOtherVersion[10]" @click="showOtherVersion(10,'job_id')" class="link-text">查看</span>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row justify="space-around">
            <el-col :span="6">
                <el-form-item label="办会专业度">
                    <span v-if="orgUserBasicInfoView.specialty">
                    	{{orgUserBasicInfoView.specialty}}
                    </span>
                    <span v-else class="yellow-color">
                    	未完善
                    </span>
                    <span v-if="!!isOtherVersion[11]" @click="showOtherVersion(11,'specialty')" class="link-text">查看</span>
                </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="办会角色">
                        <span v-if="orgUserBasicInfoView.conf_role">
                        	{{orgUserBasicInfoView.conf_role}}
                        </span>
                        <span v-else class="yellow-color">
                        	未完善
                        </span>
                       <span v-if="!!isOtherVersion[12]" @click="showOtherVersion(12,'conf_role')" class="link-text">查看</span>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="在公司角色">
                        <span v-if="orgUserBasicInfoView.company_role">
                        	{{orgUserBasicInfoView.company_role}}
                        </span>
                        <span v-else class="yellow-color">
                        	未完善
                        </span>
                        <span v-if="!!isOtherVersion[13]" @click="showOtherVersion(13,'company_role')" class="link-text">查看</span>
                    </el-form-item>
                </el-col>
        </el-row>

      <el-row>
        <el-col :span="24">
          <el-form-item label="其他备注">
            <el-row  v-if="!!isOtherVersion[15]"><span @click="showOtherVersion(15,'comment')" class="link-text">查看</span></el-row>
            <p v-if="orgUserBasicInfoView.comment">
              {{orgUserBasicInfoView.comment}}
            </p>
            <span v-else class="yellow-color">
                	未完善
                </span>
          </el-form-item>
        </el-col>
      </el-row>
        <h2>推荐人/友好关系人</h2>
        <el-row justify="space-around">
            <el-col :span="24">
            <el-form-item label="姓名">
                <span v-if="orgUserBasicInfoView.recommand_name">
                	{{orgUserBasicInfoView.recommand_name}}
                </span>
                <span v-else class="yellow-color">
                	未完善
                </span>
              <span v-if="!!isOtherVersion[14]" @click="showOtherVersion(14,'')" class="link-text other-version-text">查看</span>
              </el-form-item>
         </el-col>
        </el-row>

        <el-row class="recommand_info" justify="space-around">
            <el-col  :span="6">
            <el-form-item label="手机号码1">
                <span
                	class="cursor-pointer"
                	v-if="orgUserBasicInfoView.recommand_mobile"
                	@click="getWholeMobile('mobile',1,2);">
                	{{orgUserBasicInfoView.recommand_mobile}}
                </span>
                <span v-else class="yellow-color">
                	未完善
                </span>
              </el-form-item>
            </el-col>
            <el-col :span="6">
            <el-form-item label="手机号码2">
                <span
                	class="cursor-pointer"
                	v-if="orgUserBasicInfoView.recommand_mobile1"
                	@click="getWholeMobile('mobile1',2,2);">
                	{{orgUserBasicInfoView.recommand_mobile1}}
                </span>
                <span v-else class="yellow-color">
                	未完善
                </span>
              </el-form-item>
            </el-col>
            <el-col :span="6">
            <el-form-item label="固定号码">
                <span
                	class="cursor-pointer"
                	v-if="orgUserBasicInfoView.recommand_phone"
                	@click="getWholeMobile('phone',3,2);">
                	{{orgUserBasicInfoView.recommand_phone}}
                </span>
                <span v-else class="yellow-color">
                	未完善
                </span>
            </el-form-item>
            </el-col>
        </el-row>

        <el-row justify="space-around">
            <el-col :span="24">
              <el-form-item v-if="orgUserBasicInfoView.recommand_id!=0&&recommand_mobile" label="推荐人信息">
                <router-link :to="`/foreground/index/userview/${orgUserBasicInfoView.recommand_mobile}/${orgUserBasicInfoView.recommand_company_id}/${orgUserBasicInfoView.recommand_id}`">
                 点击查看
                </router-link>
              </el-form-item>
            </el-col>
        </el-row>


        <el-button
            type="primary"
            class="margin-bottom10"
            @click="edit();">
            修改
        </el-button>
    </el-form>
</template>

<script>

import ElRow from "element-ui/packages/row/src/row";

export default {
  components: {ElRow},
  props: ['company_id','customer_id','isOtherVersion'],
    data: function(){
        return{
            orgUserBasicInfoView: {},
            mobile: false,
            mobile_sending: false,
            mobile1: false,
            mobile1_sending: false,
            phone: false,
            phone_sending: false,
            recommand_mobile: false,
            recommand_mobile_sending: false,
            recommand_mobile1: false,
            recommand_mobile1_sending: false,
            recommand_phone: false,
            recommand_phone_sending: false
        }
    },
    created(){
        let self = this;
        self.getOrgUserBasicInfoView();
        self.resetMobileBool();
        self.$bus.$off('flashViewOne')
        self.$bus.$on('flashViewOne',self.getOrgUserBasicInfoView)
    },
    watch: {
	    '$route' (to, from) {
	        let self = this;
        	self.getOrgUserBasicInfoView();
            self.resetMobileBool();
	    }
	},
    methods: {
        getOrgUserBasicInfoView(){
            let self = this;
            self.$ajax.get(
            	self.$api.getModuleHost('CRM', "customer/getUserBasicInfo"),
            	{
            		company_id: self.company_id,
            		customer_id: self.customer_id
            	}
            )
            .then(function (data) {
                if (data.code == 1000) {
                    self.orgUserBasicInfoView = data.data;
                }
            }).catch(function (err) {
                console.error(err);
            });
        },
        resetMobileBool(){
            let self = this;
            self.mobile = false,
            self.mobile1 = false,
            self.phone = false,
            self.recommand_mobile = false,
            self.recommand_mobile1 = false,
            self.recommand_phone = false
        },
        getWholeMobile(typeStr,mobile_type,identity_type){
        	let self = this;
        	let arr = [];
            console.log(self[typeStr],self[typeStr+'_sending'])
          if(identity_type == 1){
            if(self[typeStr] || self[typeStr+'_sending']){
              return;
            }
            self[typeStr+'_sending'] = true;
          }else if(identity_type == 2){
            if(self['recommand_'+typeStr] || self[typeStr+'_sending']){
              return;
            }
            self['recommand_'+typeStr+'_sending'] = true;
          }
        	self.$ajax.get(
            	self.$api.getModuleHost('CRM', "customer/getMobile"),
            	{
            		company_id: self.company_id,
            		customer_id: self.customer_id,
            		mobile_type: mobile_type,
            		identity_type: identity_type
            	}
            )
            .then(function (data) {
                if (data.code == 1000) {

               		if(identity_type == 1){
                    self[typeStr] = true;
                    self[typeStr+'_sending'] = false;
               			arr = self.orgUserBasicInfoView[typeStr].split('****');
						arr.splice(1,0,data.data);
						self.orgUserBasicInfoView[typeStr] = arr.join('');
		        	} else if(identity_type == 2){
                    self['recommand_' + typeStr] = true;
                    self['recommand_'+typeStr+'_sending'] = false;
		        		arr = self.orgUserBasicInfoView['recommand_' + typeStr].split('****');
						arr.splice(1,0,data.data);
						self.orgUserBasicInfoView['recommand_' + typeStr] = arr.join('');
		        	}
                } else {
                	alert(data.msg);
                }
            }).catch(function (err) {
                console.error(err);
            });
        },
        edit(){
            let self = this;
            self.$emit('getBool');
        },
        showOtherVersion(id,name){
            this.$emit('otherVersionViewControl','1',id,name)
          }
    }
}
</script>
<style lang="less">
    .el-radio-group .sex-radio.el-radio{
        width: 50px;
    }
    .other-version-text{
      font-size:12px;
    }
   .recommand_info{
      padding-left:60px;
   }
</style>



