<template>
	<el-form ref="form4" label-width="140px">
		<el-row justify="space-around">
			<el-form-item label="企业常办会部门"
				v-if="orgcompanyfeatureview.conf_department.length">
        <el-row v-if="!!isOtherVersion[36]"> <span  @click="showOtherVersion(36,'conf_department')" class="link-text">版本</span></el-row>
				<span v-for="item in orgcompanyfeatureview.conf_department">
					{{item}}&nbsp;&nbsp;
				</span>
			</el-form-item>
			<el-form-item label="企业常办会部门" v-else>
				<span class="yellow-color">
		        	未完善
		        </span>
			</el-form-item>
		</el-row>

		<el-row justify="space-around">
			<el-form-item label="企业决策方式">
				<span v-if="orgcompanyfeatureview.decision_type">{{orgcompanyfeatureview.decision_type}}</span>
				<span v-else class="yellow-color">
		        	未完善
		        </span>
        <span v-if="!!isOtherVersion[37]" @click="showOtherVersion(37,'decision_type')" class="link-text">版本</span>
			</el-form-item>
		</el-row>

		<el-row justify="space-around">
			<el-form-item label="是否是采购决策人">
				<el-row>
					<span v-if="orgcompanyfeatureview.is_decisionmaker">
						{{orgcompanyfeatureview.is_decisionmaker}}
					</span>
					<span v-else class="yellow-color">
			        	未完善
			        </span>
          <span v-if="!!isOtherVersion[38]" @click="showOtherVersion(38,'is_decisionmaker')" class="link-text">版本</span>
				</el-row>
				<div v-if="orgcompanyfeatureview.is_decisionmaker!='是'">
          <el-row>
            <el-col>
              <el-form-item v-if="!!isOtherVersion[39]" label="决策人信息" class="text-align-left">
                <span  @click="showOtherVersion(39,'decision_user')" class="link-text">版本</span>
              </el-form-item>
            </el-col>
          </el-row>
					<el-row>
						<el-col :span="8">
							<el-form-item label="决策人姓名" class="text-align-left">
								<span v-if="orgcompanyfeatureview.decision_user.name">{{orgcompanyfeatureview.decision_user.name}}</span>
								<span v-else class="yellow-color">
				        	未完善
				        </span>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item label="决策人所在部门" class="text-align-left">
								<span v-if="orgcompanyfeatureview.decision_user.department">{{orgcompanyfeatureview.decision_user.department}}</span>
								<span v-else class="yellow-color">
				        	未完善
				        </span>
							</el-form-item>
						</el-col>
					</el-row>
					<el-row>
						<el-col :span="8">
							<el-form-item label="决策人职位" class="text-align-left">
								<span v-if="orgcompanyfeatureview.decision_user.job">{{orgcompanyfeatureview.decision_user.job}}</span>
								<span v-else class="yellow-color">
				        	未完善
				        </span>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item label="决策人联系方式" class="text-align-left">
								<span v-if="orgcompanyfeatureview.decision_user.phone"
                  @click="getWholeMobile('mobile1',2,2)" class="cursor-pointer">{{orgcompanyfeatureview.decision_user.phone}}</span>
								<span v-else class="yellow-color">
				        	未完善
				        </span>
							</el-form-item>
						</el-col>
					</el-row>
				</div>
			</el-form-item>
		</el-row>

		<el-row>
			<el-form-item label="了解到客户内部的采购决策情况" label-width="210px">
        <span v-if="!!isOtherVersion[40]" @click="showOtherVersion(40,'decision_comment')" class="link-text">版本</span>
			</el-form-item>
			<el-form-item>
				<p v-if="orgcompanyfeatureview.decision_comment">
					{{orgcompanyfeatureview.decision_comment}}
				</p>
				<p v-else class="yellow-color">
					未完善
				</p>
			</el-form-item>
		</el-row>

		<el-button
            type="primary"
            class="margin-bottom10"
            @click="edit();">
            修改
        </el-button>
	</el-form>
</template>

<script>

import ElForm from "../../../node_modules/element-ui/packages/form/src/form.vue";
import ElFormItem from "../../../node_modules/element-ui/packages/form/src/form-item.vue";
import ElRow from "element-ui/packages/row/src/row";

export default {
  components: {
    ElRow,
    ElFormItem,
    ElForm},
  props: ['company_id','customer_id','isOtherVersion'],
	data: function () {
		return {
      decision_phone:false,
			orgcompanyfeatureview:{
				conf_department: [],
				decision_comment: '',
				decision_type: '',
				decision_user: {
					name: '',
					department: '',
					job: '',
					phone: ''
				},
				is_decisionmaker: ''
			}
		}
	},
	created(){
		let self = this;
		self.getOrgCompanyFeatureView();
    self.$bus.$off('flashViewFour')
    self.$bus.$on('flashViewFour',self.getOrgCompanyFeatureView)
	},
	watch: {
	    '$route' (to, from) {
	  		let self = this;
				self.resetData();
				self.getOrgCompanyFeatureView();
	    }
	},
	methods: {
		resetData() {
			this.orguserfeatureview = {
				prev_year_budget: '',
				next_conf_info: '',
				allopatry_conf: '',
				current_year_num: '',
				hxe_percentage: '',
				allopatry_conf_info: [
					{
						id: '',
						num: '',
						price:''
					},
					{
						id: '',
						num: '',
						price:''
					}
				],
				often_conf_type: [],
				often_conf_city: [],
				occasionally_conf_city: [],
				participant_type: [],
				occasionally_conf_city: [],
				consumption_interval: [],
				reason: ''
			}
		},
		getOrgCompanyFeatureView(){
			let self = this;
			self.$ajax.get(
				self.$api.getModuleHost('CRM', "customer/getUserCompanyFeature"),
				{
		      		company_id: self.company_id,
		      		customer_id: self.customer_id
		      	}
			)
			.then(function (data) {
				if (data.code == 1000) {
					self.orgcompanyfeatureview.decision_comment = data.data.decision_comment;
					self.orgcompanyfeatureview.decision_type = data.data.decision_type;
					self.orgcompanyfeatureview.is_decisionmaker = data.data.is_decisionmaker;
					if(data.data.conf_department!=''){
						self.orgcompanyfeatureview.conf_department = data.data.conf_department.split(',');
					}
					if(data.data.decision_user != ''){
						self.orgcompanyfeatureview.decision_user = data.data.decision_user;
					}
				}
			}).catch(function (err) {
				console.error(err);
			});
		},
		edit(){
			let self = this;
            self.$emit('getBool');
		},
    showOtherVersion(id,name){
      this.$emit('otherVersionViewControl','1',id,name)
    },
    getWholeMobile(){
      let self = this;
      if (self.decision_phone) return
      self.decision_phone=true
      self.$ajax.get(
        self.$api.getModuleHost('CRM', "customer/getMobile"),
        {
          company_id: self.company_id,
          customer_id: self.customer_id,
          mobile_type: 3,
          identity_type: 3
        }
        )
        .then(function (data) {
          if (data.code == 1000) {
            let number = self.orgcompanyfeatureview.decision_user.phone.split('****');
            self.orgcompanyfeatureview.decision_user.phone=number[0]+data.data+number[1]
          } else {
            self.decision_phone=false
            alert(data.msg);
          }
        }).catch(function (err) {
        self.decision_phone=false
        console.error(err);
      });
    }
	}
}
</script>
<style lang="less">

</style>



