<template>
	<el-form ref="form3" label-width="140px">
		<el-row justify="space-around">
			<el-form-item label="偏好场地类型"
				v-if="orguserfeatureview.site_type.length">
        <el-row v-if="!!isOtherVersion[27]"><span  @click="showOtherVersion(27,'site_type')" class="link-text">版本</span></el-row>
				<span v-for="item in orguserfeatureview.site_type">
					{{item}}&nbsp;&nbsp;
				</span>
			</el-form-item>
			<el-form-item label="偏好场地类型" v-else>
        <el-row v-if="!!isOtherVersion[27]"><span  @click="showOtherVersion(27,'site_type')" class="link-text">版本</span></el-row>
					<span class="yellow-color">
	        	未完善
	        </span>
			</el-form-item>
		</el-row>

		<el-row justify="space-around">
			<el-form-item label="偏好调性" v-if="orguserfeatureview.preference.length">
        <el-row v-if="!!isOtherVersion[28]"><span  @click="showOtherVersion(28,'preference')" class="link-text">版本</span></el-row>
				<span v-for="item in orguserfeatureview.preference">
					{{item}}&nbsp;&nbsp;
				</span>
			</el-form-item>
			<el-form-item v-else label="偏好调性">
        <el-row v-if="!!isOtherVersion[28]"><span  @click="showOtherVersion(28,'preference')" class="link-text">版本</span></el-row>
				<span class="yellow-color">
        	未完善
        </span>
			</el-form-item>
		</el-row>

		<el-row justify="space-around">
			<el-form-item label="经常使用场地区域"
				v-if="orguserfeatureview.often_conf_area.length">
        <el-row v-if="!!isOtherVersion[29]">  <span   @click="showOtherVersion(29,'often_conf_area')" class="link-text">版本</span></el-row>
				<el-row v-for="item in orguserfeatureview.often_conf_area">
					<span>{{item.name}}</span>
					<p v-if="item.comment">原因 : {{item.comment}}</p>
					<p v-else>原因 :
						<span class="yellow-color">未完善</span>
					</p>
				</el-row>
			</el-form-item>
			<el-form-item label="经常使用场地区域" v-else>
				<el-row class="yellow-color">
		        	<span>未完善</span>
          <span v-if="!!isOtherVersion[29]"  @click="showOtherVersion(29,'often_conf_area')" class="link-text">版本</span>
		        </el-row>
			</el-form-item>
		</el-row>

		<el-row justify="space-around">
			<el-form-item label="决策快慢">
        <el-row v-if="!!isOtherVersion[30]"><span  @click="showOtherVersion(30,'decision')" class="link-text">版本</span></el-row>
				<el-row v-if="orguserfeatureview.decision">
					<el-col :span="8">
						<span>{{orguserfeatureview.decision}}</span>
					</el-col>
					<el-col :span="8">
						<span v-if="orguserfeatureview.decision_reason">
							原因：{{orguserfeatureview.decision_reason}}
						</span>
						<span v-else-if="orguserfeatureview.decision!='中'">
							原因：<span class="yellow-color">未完善</span>
						</span>
					</el-col>
				</el-row>
				<el-row v-else>
					<el-col :span="8" class="yellow-color">
						<span>未完善</span>
					</el-col>
				</el-row>
			</el-form-item>
		</el-row>

		<el-row justify="space-around">
			<el-form-item label="客户价格敏感度">
        <el-row  v-if="!!isOtherVersion[31]"><span @click="showOtherVersion(31,'participant')" class="link-text">版本</span></el-row>
				<el-row>
					<p v-if="orguserfeatureview.participant[0].name!=''">{{orguserfeatureview.participant[0].name}}</p>
					<p v-else class="yellow-color">
	        	未完善
	        </p>
				</el-row>
				<el-row>
					<p v-if="orguserfeatureview.participant[0].comment !=''">原因：{{orguserfeatureview.participant[0].comment}}</p>
					<p v-else class="yellow-color">
	        	未完善
	        </p>
				</el-row>
			</el-form-item>
		</el-row>

		<el-row justify="space-around">
			<el-form-item label="房餐会次数占比">
        <el-row  v-if="!!isOtherVersion[32]"><span @click="showOtherVersion(32,'percentage')" class="link-text">版本</span></el-row>
				<el-row>
					<el-col :span="6">
						<el-form-item label="房餐会" class="text-align-left"></el-form-item>
						<span v-if="orguserfeatureview.percentage.percentage">{{orguserfeatureview.percentage.percentage}}&nbsp;%</span>
						<span v-else class="yellow-color">
		        	未完善
		        </span>
					</el-col>
					<el-col :span="6">
						<el-form-item label="纯会、餐会、房会" class="text-align-left"></el-form-item>
						<span v-if="orguserfeatureview.percentage.percentage">{{100 - orguserfeatureview.percentage.percentage}}&nbsp;%</span>
						<span v-else class="yellow-color">
		        	未完善
		        </span>
					</el-col>
				</el-row>
				<el-row >
					<el-col :span="6">
						<el-form-item label="房餐会次数" class="text-align-left"></el-form-item>
						<span v-if="orguserfeatureview.percentage.num">{{orguserfeatureview.percentage.num}}&nbsp;次</span>
						<span v-else class="yellow-color">
		        	未完善
		        </span>
					</el-col>
					<el-col  :span="6">
						<el-form-item label="房餐会平均每场"  class="text-align-left"></el-form-item>
						<span  v-if="orguserfeatureview.percentage.price">{{orguserfeatureview.percentage.price}}&nbsp;万</span>
						<span v-else class="yellow-color">
		        	未完善
		        </span>
					</el-col>
				</el-row>
			</el-form-item>
		</el-row>

		<el-row justify="space-around">
			<el-form-item label="是否有策划搭建需求">
        <el-row v-if="!!isOtherVersion[33]" ><span @click="showOtherVersion(33,'build_require')" class="link-text">版本</span></el-row>
				<el-row>
					<p>
						<span v-if="orguserfeatureview.build_require">
							{{orguserfeatureview.build_require}}
						</span>
						<span v-else class="yellow-color">
				        	未完善
				        </span>
					</p>
				</el-row>
				<div v-if="orguserfeatureview.build_require!='没有'">
					<el-row v-if="orguserfeatureview.build_require_type.length">
						<span v-for="item in orguserfeatureview.build_require_type">
							{{item}}&nbsp;&nbsp;
						</span>
					</el-row>
					<el-row v-else>
						<span class="yellow-color">
							未完善
						</span>
					</el-row>
				</div>
			</el-form-item>
		</el-row>

		<el-row justify="space-around">
			<el-form-item label="客户特质">
				<el-row>
					<span v-if="orguserfeatureview.trait">{{orguserfeatureview.trait}}</span>

					<span v-else class="yellow-color">
						未完善
					</span>
          <span v-if="!!isOtherVersion[34]" @click="showOtherVersion(34,'trait')" class="link-text">版本</span>
				</el-row>
			</el-form-item>
		</el-row>

		<el-row justify="space-around">
			<el-form-item label="客户个性标签">
        <el-row v-if="!!isOtherVersion[35]" >  <span @click="showOtherVersion(35,'customer_tag')" class="link-text">版本</span></el-row>
				<el-row v-if="orguserfeatureview.customer_tag.length">
					<span v-for="item in orguserfeatureview.customer_tag">
						{{item}}&nbsp;&nbsp;
					</span>
				</el-row>
				<el-row v-else>
					<span class="yellow-color">
						未完善
					</span>
				</el-row>
			</el-form-item>
		</el-row>

		<el-button
            type="primary"
            class="margin-bottom10"
            @click="edit();">
            修改
        </el-button>
	</el-form>
</template>

<script>

import ElRow from "element-ui/packages/row/src/row";

export default {
  components: {ElRow},
  props: ['company_id','customer_id','isOtherVersion'],
	data: function () {
		return {
			orguserfeatureview: {
				decision: '',
				decision_reason:'',
				build_require: '',
				trait: '',
				site_type: [],
				preference: [],
				build_require_type: [],
				customer_tag: [],
				often_conf_area: [],
				participant: [
					{
						name: '',
						comment: ''
					}
				],
				percentage: {
					percentage : '',
					num: '',
					price: ''
				}
			}
		}
	},
	created(){
		let self = this;
		self.getOrgUserFeatureView();
    self.$bus.$off('flashViewThree')
    self.$bus.$on('flashViewThree',self.getOrgUserFeatureView)

	},
	watch: {
	    '$route' (to, from) {
	  		let self = this;
				self.resetData();
				self.getOrgUserFeatureView();
	    }
	},
	methods: {
		resetData() {
			this.orguserfeatureview = {
				decision: '',
				decision_reason:'',
				build_require: '',
				trait: '',
				site_type: [],
				preference: [],
				build_require_type: [],
				customer_tag: [],
				often_conf_area: [],
				participant: [
					{
						name: '',
						comment: ''
					}
				],
				percentage: {
					percentage : '',
					num: '',
					price: ''
				}
			}
		},
		getOrgUserFeatureView(){
			let self = this;
			self.$ajax.get(
				self.$api.getModuleHost('CRM', "customer/getUserFeature"),
				{
		      		company_id: self.company_id,
		      		customer_id: self.customer_id
		      	}
			)
			.then(function (data) {
				if (data.code == 1000) {

					self.orguserfeatureview.decision = data.data.decision;
					self.orguserfeatureview.decision_reason = data.data.decision_reason;
					self.orguserfeatureview.build_require = data.data.build_require;
					self.orguserfeatureview.trait = data.data.trait;

					if(data.data.site_type!=''){
						self.orguserfeatureview.site_type = data.data.site_type.split(',');
					}
					if(data.data.preference!=''){
						self.orguserfeatureview.preference = data.data.preference.split(',');
					}
					if(data.data.build_require_type!=''){
						self.orguserfeatureview.build_require_type = data.data.build_require_type.split(',');
					}
					if(data.data.customer_tag!=''){
						self.orguserfeatureview.customer_tag = data.data.customer_tag.split(',');
					}
					if(data.data.percentage!=''){
						self.orguserfeatureview.percentage = data.data.percentage;
					}

					if(data.data.participant.length){
						self.orguserfeatureview.participant = data.data.participant;
					}

					self.orguserfeatureview.often_conf_area = data.data.often_conf_area;
				}
			}).catch(function (err) {
				console.error(err);
			});
		},
		edit(){
            let self = this;
            self.$emit('getBool');
        },
    showOtherVersion(id,name){
      this.$emit('otherVersionViewControl','1',id,name)
    }
	},
	computed: {

	}
}
</script>
<style lang="less">
	.el-rate{
		margin-top: 8px;
	}
</style>



