<template>
	<el-form ref="form2" label-width="140px">
		<el-row justify="space-around">
			<el-col :span="8">
				<el-form-item label="2016年会议总预算">
					<span v-if="orgreqinfoview.prev_year_budget">
						{{orgreqinfoview.prev_year_budget}} 万
					</span>
					<span v-else class="yellow-color">未完善</span>
          <span v-if="!!isOtherVersion[16]" @click="showOtherVersion(16,'prev_year_budget')" class="link-text">版本</span>
				</el-form-item>
			</el-col>
			<el-col :span="8">
				<el-form-item label="2017年会议总次数">
					<span v-if="orgreqinfoview.current_year_num">
						{{orgreqinfoview.current_year_num}} 次
					</span>
					<span v-else class="yellow-color">未完善</span>
          <span v-if="!!isOtherVersion[17]" @click="showOtherVersion(17,'current_year_num')" class="link-text">版本</span>
				</el-form-item>
			</el-col>
		</el-row>

		<el-row justify="space-around">
			<el-col :span="24">
				<el-form-item label="2018年预计会议">
          <el-row  v-if="!!isOtherVersion[18]" > <span @click="showOtherVersion(18,'next_conf_info')" class="link-text">版本</span></el-row>
					<el-row>
						<span v-if="orgreqinfoview.next_conf_info">
							{{orgreqinfoview.next_conf_info}}
						</span>
						<span v-else class="yellow-color">未完善</span>

					</el-row>
					<el-row>
						原因:
						<span v-if="orgreqinfoview.reason">
							{{orgreqinfoview.reason}}
						</span>
						<span v-else class="yellow-color">未完善</span>
					</el-row>
				</el-form-item>
			</el-col>
		</el-row>

		<el-row justify="space-around">
			<el-col :span="8">
				<el-form-item label="通过会小二的预定金额占比">
					<span v-if="orgreqinfoview.hxe_percentage">
						{{orgreqinfoview.hxe_percentage}} %
					</span>
					<span v-else class="yellow-color">未完善</span>
          <span v-if="!!isOtherVersion[19]" @click="showOtherVersion(19,'hxe_percentage')" class="link-text">版本</span>
				</el-form-item>
			</el-col>
		</el-row>

		<el-row justify="space-around">
			<el-form-item label="举办会议消费区间" v-if="orgreqinfoview.consumption_interval instanceof Array&&orgreqinfoview.consumption_interval.length>0">
        <el-row v-if="!!isOtherVersion[20]"> <span  @click="showOtherVersion(20,'consumption_interval')" class="link-text">版本</span></el-row>
				<el-col :span="8" class="margin-bottom10"
					v-for="item in orgreqinfoview.consumption_interval">
					<span>{{item}}</span>
				</el-col>
			</el-form-item>
			<el-form-item v-else label="举办会议消费区间" class="short-input">
        <el-row v-if="!!isOtherVersion[20]"> <span  @click="showOtherVersion(20,'consumption_interval')" class="link-text">版本</span></el-row>
				<span class="yellow-color">未完善</span>
			</el-form-item>
		</el-row>

		<el-row justify="space-around">
			<el-form-item label="经常举办会议类型">
        <el-row v-if="!!isOtherVersion[21]">  <span  @click="showOtherVersion(21,'often_conf_type')" class="link-text">版本</span></el-row>
				<el-row justify="space-around" v-if="orgreqinfoview.often_conf_type!=''">
					<span v-for="item in orgreqinfoview.often_conf_type">
						{{item}}&nbsp;&nbsp;
					</span>
				</el-row>
				<el-row v-else justify="space-around">
					<span class="yellow-color">未完善</span>
				</el-row>
			</el-form-item>
		</el-row>

		<el-row justify="space-around">
			<el-form-item label="是否要异地办会">
				<el-row>
					<span v-if="orgreqinfoview.allopatry_conf">
						{{orgreqinfoview.allopatry_conf}}
					</span>
					<span v-else class="yellow-color">未完善</span>
          <span v-if="!!isOtherVersion[22]" @click="showOtherVersion(22,'allopatry_conf')" class="link-text">版本</span>
				</el-row>
				<div v-show="orgreqinfoview.allopatry_conf!='无'">
					<el-row>
						<el-form-item
							label="常跨地区举办的两种会议"
							label-width="180px"
							class="text-align-left">
              <span v-if="!!isOtherVersion[23]" @click="showOtherVersion(23,'allopatry_conf_info')" class="link-text">版本</span>
						</el-form-item>
					</el-row>
					<el-row class="margin-bottom10">
						<el-col :span="8">
							<span v-if="orgreqinfoview.allopatry_conf_info[0].id">
								{{orgreqinfoview.allopatry_conf_info[0].id}}
							</span>
							<span v-else class="yellow-color">未完善</span>
						</el-col>
						<el-col :span="8">
							<el-form-item label="平均消费金额">
								<span v-if="orgreqinfoview.allopatry_conf_info[0].price">
									{{orgreqinfoview.allopatry_conf_info[0].price}} 万
								</span>
								<span v-else class="yellow-color">未完善</span>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item label="大致次数">
								<span v-if="orgreqinfoview.allopatry_conf_info[0].num">
									{{orgreqinfoview.allopatry_conf_info[0].num}} 次
								</span>
								<span v-else class="yellow-color">未完善</span>
							</el-form-item>
						</el-col>
					</el-row>
					<el-row>
						<el-col :span="8">
							<span v-if="orgreqinfoview.allopatry_conf_info[1].id">
								{{orgreqinfoview.allopatry_conf_info[1].id}}
							</span>
							<span v-else class="yellow-color">未完善</span>
						</el-col>
						<el-col :span="8">
							<el-form-item label="平均消费金额">
								<span v-if="orgreqinfoview.allopatry_conf_info[1].price">
									{{orgreqinfoview.allopatry_conf_info[1].price}} 万
								</span>
								<span v-else class="yellow-color">未完善</span>
							</el-form-item>
						</el-col>
						<el-col :span="8">
							<el-form-item label="大致次数">
								<span v-if="orgreqinfoview.allopatry_conf_info[1].num">
									{{orgreqinfoview.allopatry_conf_info[1].num}} 次
								</span>
								<span v-else class="yellow-color">未完善</span>
							</el-form-item>
						</el-col>
					</el-row>
					<el-row>
						<el-form-item label="常举办城市" class="text-align-left">
              <span v-if="!!isOtherVersion[24]" @click="showOtherVersion(24,'often_conf_city')" class="link-text">版本</span>
						</el-form-item>
					</el-row>
					<el-row v-if="orgreqinfoview.often_conf_city!=''">
						<span v-for="item in orgreqinfoview.often_conf_city">
							{{item}}&nbsp;&nbsp;
						</span>
					</el-row>
					<el-row v-else>
						<span class="yellow-color">未完善</span>
					</el-row>
					<el-row>
						<el-form-item label="有时举办城市" class="text-align-left">
              <span v-if="!!isOtherVersion[25]" @click="showOtherVersion(25,'occasionally_conf_city')" class="link-text">版本</span>
						</el-form-item>
					</el-row>
					<el-row v-if="orgreqinfoview.occasionally_conf_city!=''">
						<span v-for="item in orgreqinfoview.occasionally_conf_city">
							{{item}}&nbsp;&nbsp;
						</span>
					</el-row>

					<el-row v-else>
						<span class="yellow-color">未完善</span>

					</el-row>
				</div>
			</el-form-item>
		</el-row>

		<el-row justify="space-around">
			<el-form-item label="会议参会人类型"
				v-if="orgreqinfoview.participant_type!=''">
        <el-row v-if="!!isOtherVersion[26]"><span  @click="showOtherVersion(26,'participant_type')" class="link-text">版本</span></el-row>
				<span v-for="item in orgreqinfoview.participant_type">
					{{item}}&nbsp;&nbsp;
				</span>
			</el-form-item>
			<el-form-item v-else label="会议参会人类型">
        <el-row v-if="!!isOtherVersion[26]"><span  @click="showOtherVersion(26,'participant_type')" class="link-text">版本</span></el-row>
				<span class="yellow-color">未完善</span>
			</el-form-item>
		</el-row>

		<el-button
            type="primary"
            class="margin-bottom10"
            @click="edit();">
            修改
        </el-button>
	</el-form>
</template>

<script>
import ElRow from "element-ui/packages/row/src/row";

export default {
  components: {ElRow},
  props: [
		'company_id',
		'customer_id',
		'baseData',
    'isOtherVersion'
	],
	data: function(){
		return {
			orgreqinfoview: {
				prev_year_budget: '',
				next_conf_info: '',
				allopatry_conf: '',
				current_year_num: '',
				hxe_percentage: '',
				allopatry_conf_info: [
					{
						id: '',
						num: '',
						price:''
					},
					{
						id: '',
						num: '',
						price:''
					}
				],
				often_conf_type: [],
				often_conf_city: [],
				occasionally_conf_city: [],
				participant_type: [],
				occasionally_conf_city: [],
				consumption_interval: [],
				reason: ''
			}
		}
	},
	created(){
		let self = this;
		self.getOrgReqInfoView();
    self.$bus.$off('flashViewTwo')
    self.$bus.$on('flashViewTwo',self.getOrgReqInfoView)
	},
	watch: {
  		'$route' (to, from) {
	  		let self = this;
				self.resetData();
				self.getOrgReqInfoView();
	    }
  },

	methods: {
		resetData() {
			this.orgreqinfoview = {
				prev_year_budget: '',
				next_conf_info: '',
				allopatry_conf: '',
				current_year_num: '',
				hxe_percentage: '',
				allopatry_conf_info: [
					{
						id: '',
						num: '',
						price:''
					},
					{
						id: '',
						num: '',
						price:''
					}
				],
				often_conf_type: [],
				often_conf_city: [],
				occasionally_conf_city: [],
				participant_type: [],
				occasionally_conf_city: [],
				consumption_interval: [],
				reason: ''
			}
		},
		getOrgReqInfoView(){
			let self = this;
			self.$ajax.get(
				self.$api.getModuleHost('CRM', "customer/getReqInfo"),
				{
		      		company_id: self.company_id,
		      		customer_id: self.customer_id
		      	}
			)
			.then(function (data) {
				if (data.code == 1000) {
					//self.orgreqinfoview = data.data;
					self.orgreqinfoview.prev_year_budget = data.data.prev_year_budget;
					self.orgreqinfoview.next_conf_info = data.data.next_conf_info;
					self.orgreqinfoview.allopatry_conf = data.data.allopatry_conf;
					self.orgreqinfoview.current_year_num = data.data.current_year_num;
					self.orgreqinfoview.hxe_percentage = data.data.hxe_percentage;
					self.orgreqinfoview.reason = data.data.reason;

					if(data.data.participant_type != ''){
						self.orgreqinfoview.participant_type = data.data.participant_type.split(',');
					}
					if(data.data.often_conf_type != ''){
						self.orgreqinfoview.often_conf_type = data.data.often_conf_type.split(',');
					}
					if(data.data.often_conf_city != ''){
						self.orgreqinfoview.often_conf_city = data.data.often_conf_city.split(',');
					}
					if(data.data.occasionally_conf_city != '' ){
						self.orgreqinfoview.occasionally_conf_city = data.data.occasionally_conf_city.split(',');
					}

					if(data.data.allopatry_conf_info[0]){
						self.orgreqinfoview.allopatry_conf_info[0] = data.data.allopatry_conf_info[0];
					}

					if(data.data.allopatry_conf_info[1]){
						self.orgreqinfoview.allopatry_conf_info[1] = data.data.allopatry_conf_info[1];
					}

					if(data.data.consumption_interval){
						self.orgreqinfoview.consumption_interval = data.data.consumption_interval;
					}

				}
			}).catch(function (err) {
				console.error(err);
			});
		},
    showOtherVersion(id,name){
      this.$emit('otherVersionViewControl','1',id,name)
    },
		edit(){
            let self = this;
            self.$emit('getBool');
        }
	}
}
</script>
<style lang="less">

</style>



