<template>
    <div   :class="{otherVersionBoxHide:otherVersionHide}" class="VersionBoxContent">
      <div  :class="{otherVersionBoxHide:otherVersionHide}" class="otherVersionBox">
        <h3  class="otherVersionBoxTitle">查看其他版本<span class="tip">如有你认为正确的版本，可勾选选择同意</span></h3>

        <span  class="otherVersionClose el-icon-close pointer" @click="otherVersionClose"></span>
  <!--        <div>
            <el-row>
              {{selectedIndex}}
              <el-radio class="radio" v-model="selectedIndex" label="0">&nbsp;300万</el-radio>
            </el-row>
            <el-row>
              <el-col :span="4">王专员</el-col>
              <el-col :span="20">创建时间：<span>2017.7.3 21:00:00</span></el-col>
            </el-row>
            </el-row>
          </div>-->
        <div v-loading="isLoading" class="item-content">
          <div  v-for="(item,index) in otherVersionData.show">
            <el-row>
              <el-radio class="radio" v-model="selectedIndex" :label="item.order">&nbsp;<span v-for="val in item.index_name">[&nbsp;{{val}}&nbsp;]&nbsp;</span></el-radio>
            </el-row>
            <el-row v-for="person in item.info">
              <el-col :span="4">{{person.admin_name}}</el-col>
              <el-col :span="20">创建时间：<span>{{person.create_time}}</span></el-col>
            </el-row>
          </div>
        </div>
          <el-button :disabled="selectedIndex==-1"  class="agreeRollBack" @click="VersonGoBack(selectedIndex)" type="primary" size="small" >同意</el-button>

        </div>
        <div :class="{mark:!otherVersionHide}"></div>
      </div>
</template>
<script>
  export default {
    name:'otherVersion',
    props:['otherVersionHide','otherVersionCache'],
    data(){
      return{
        selectedIndex:-1,
        isLoading:true,
        otherVersionData:{}
      }
    },
    watch:{
      "otherVersionCache"(){//数变动时取消loading效果
        var keys = Object.keys(this.otherVersionCache)
        if (keys.length>0)
        setTimeout(()=>{
          this.isLoading=false
          this.otherVersionData=this.otherVersionCache
        },300)
      }
    },
    methods:{
      otherVersionClose(){
        this.isLoading=true
        this.otherVersionData={}
        this.$emit('otherVersionViewControl','0')
        this.selectedIndex=-1
      },
      VersonGoBack(index){
        this.isLoading=true
        this.otherVersionData={}
        this.$emit('VersonGoBack',index)
        this.selectedIndex=-1
      }
    }
  }
</script>
<style lang="less" >

  .VersionBoxContent{
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    text-align: center;
    z-index:1;
    .el-loading-spinner{
      top:50%;
    }
  }
  .item-content{
    padding-right:20px;
    height:270px;
    overflow:auto;
  }
  .otherVersionBoxTitle{
    padding-top:10px;
    padding-bottom:10px;
    color:#48576a;
  }
  .otherVersionBox{
    text-align:left;
    padding:0 0 50px 20px;
    z-index:999;
    background-color:#ffffff;
    border:1px solid #d1dbe5;
    border-radius:3px;
    display:inline-block;
    width:420px;
    min-height:200px;
    position:fixed;
    margin-top:-210px;
    margin-left:-250px;
    top:50%;
    .el-row{
      margin-bottom:10px;
    }
    .el-radio{
      white-space:normal;
    }
    .otherVersionClose{
      display:block;
      width:10px;
      height:10px;
      position:absolute;
      top:10px;
      right:10px;
      z-index:10001;
    }
    .agreeRollBack{
      position:absolute;
      bottom:10px;
      left:20px;
    }
    .tip{
      padding-left:10px;
      margin-bottom:10px;
      color:#ff4949;
      font-size:12px;
    }
  }
  .otherVersionBoxHide{
    display:none;
  }
  .mark{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: .5;
    background: #000;
  }

</style>



